                            class="u-avatar"
                            :src="avatar(user.avatar)"
                            :alt="user.name"
                        />
                        <span class="u-dropdown"></span>
                        <ul class="u-menu" v-show="!fold">
                            <li>
                                <a :href="JX3BOX.__Links.dashboard.home"
                                    >个人中心</a
                                >
                            </li>
                            <li>
                                <a :href="JX3BOX.__Links.dashboard.profile"
