<template>
    <div class="c-github-repo" :class="{ mini: MINI }">
        <a class="u-repo" :href="html_url" target="_blank">
            <i class="i-github"
                ><svg class="octicon octicon-mark-github v-align-middle" height="32" viewBox="0 0 16 16" width="32" aria-hidden="true" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0016 8c0-4.42-3.58-8-8-8z"/></svg></i>
            <span class="u-name">{{ full_name }}</span>
        </a>
        <div class="u-detail">
            <time class="u-update">Last updated:{{ updated_at }}</time>
            <h5 class="u-contributors">Contributors</h5>
            <ul class="u-list" v-if="!coder">
                <li v-for="(c, i) in contributors" :key="c + i">
                    <a :href="c.html_url" :title="c.login" target="_blank">
                        <img :src="c.avatar_url" :alt="c.login" />
                        <span>{{ c.contributions }}</span>
                    </a>
                </li>
            </ul>
            <ul class="u-list" v-if="coder">
                <li v-for="(c, i) in coders" :key="c + i">
                    <a
                        :href="author_link + '?uid=' + c.ID"
                        :title="c.display_name"
                        target="_blank"
                    >
                        <img
                            :src="c.user_avatar | resolveAvatarPath"
                            :alt="c.display_name"
                        />
                    </a>
                </li>
            </ul>
            <slot></slot>
        </div>
        <div class="u-more">
            <a class="u-report" :href="issue_url" target="_blank"
                >提建议 | 发现Bug</a
            >
            <a class="u-join" :href="html_url" target="_blank">+ Contribute</a>
        </div>
    </div>
</template>

<script>
import {
    __ossRoot,
    __ossMirror,
    __Links,
} from "@jx3box/jx3box-common/js/jx3box.json";
import { getRepoInfo, getBuilders, getRepoCoders } from "../service/github";
export default {
    name: "Github_REPO",
    props: ["REPO", "MINI", "coder"],
    data: function() {
        return {
            updated_at: "",
            contributors: [],
            coders: [],
            author_link: __Links.author,
        };
    },
    computed: {
        full_name: function() {
            return `JX3BOX/${this.REPO}`;
        },
        html_url: function() {
            return `https://github.com/JX3BOX/${this.REPO}`;
        },
        issue_url: function() {
            return this.html_url + "/issues";
        },
    },
    filters: {
        resolveAvatarPath: function(val) {
            return val ? val.replace(__ossRoot, __ossMirror) : "";
        },
    },
    methods: {
        getBasicInfo: function() {
            getRepoInfo(this.REPO).then((res) => {
                let data = res.data;
                if (data) this.updated_at = data.updated_at;
            });
        },
        getCoders: function() {
            getBuilders(this.coder).then((res) => {
                let data = res.data.data.list;
                let sequence = this.coder.split(",");
                this.coders = [];
                for (let uid of sequence) {
                    for (let coder of data) {
                        if (coder.ID == uid) {
                            this.coders.push(coder);
                        }
                    }
                }
            });
        },
        getRepoCoders: function() {
            getRepoCoders(this.REPO).then((res) => {
                let data = res.data;
                this.contributors = data;
            });
        },
        init: function() {
            this.getBasicInfo();
            this.coder ? this.getCoders() : this.getRepoCoders();
        },
    },
    mounted: function() {
        this.init();
    },
};
</script>

<style lang="less">
@import "../assets/css/github-repo.less";
</style>
