<template>
    <div class="c-breadcrumb">
        <div class="u-menu" @click.stop="toggleLeftSide">
            <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 12 16" width="18" aria-hidden="true" v-bind:class="'u-toggle'" :class="{ on: isOpen }" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M11.41 9H.59C0 9 0 8.59 0 8c0-.59 0-1 .59-1H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1h.01zm0-4H.59C0 5 0 4.59 0 4c0-.59 0-1 .59-1H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1h.01zM.59 11H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1H.59C0 13 0 12.59 0 12c0-.59 0-1 .59-1z"/></svg>
        </div>
        <a class="u-channel" :href="root">
            <i class="u-channel-logo"><slot name="logo"></slot></i>
            <span class="u-title">{{ name }}</span>
        </a>
        <slot></slot>
        <a
            v-if="publishEnable"
            :href="slug | publish_url(slug)"
            class="u-publish el-button el-button--primary el-button--medium"
            ><i class="el-icon-edit-outline"></i><span>发布</span></a
        >
        <a
            v-if="feedbackEnable"
            v-show="isNotAdmin"
            :href="feedback"
            class="u-feedback el-button el-button--primary el-button--medium"
            target="_blank"
            ><i class="el-icon-info"></i><span>反馈</span></a
        >
        <Adminbutton v-if="adminEnable" class="u-admin" />
        <Admin v-if="adminEnable" />
    </div>
</template>

<script>
import Bus from "../service/bus";
import { __Links, feedback } from "@jx3box/jx3box-common/js/jx3box.json";
import Admin from "./Admin";
import Adminbutton from "./Adminbutton";
import User from "@jx3box/jx3box-common/js/user";
export default {
    name: "Breadcrumb",
    props: [
        "name",
        "slug",
        "root",
        "publishEnable",
        "adminEnable",
        "feedbackEnable",
    ],
    data: function() {
        return {
            isOpen: true,
            feedback,
            isNotAdmin: User.getInfo().group < 60,
        };
    },
    computed: {},
    filters: {
        publish_url: function(val) {
            return __Links.dashboard.publish + "#/" + val;
        },
    },
    methods: {
        toggleLeftSide: function() {
            let status = !this.isOpen;
            Bus.$emit("toggleLeftSide", status);
        },
    },
    mounted: function() {
        Bus.$on("toggleLeftSide", (data) => {
            this.isOpen = data;
        });

        if (window.innerWidth < 1024) {
            this.isOpen = false;
        }
    },
    components: {
        Admin,
        Adminbutton,
    },
};
</script>

<style lang="less">
@import "../assets/css/breadcrumb.less";
</style>
